import { Taxonomy } from './../core/models/taxonomy';
import { environment } from './../../environments/environment';
import { ProductActions } from './../product/actions/product-actions';
import { AppState } from './../interfaces';
import { getProducts } from './../product/reducers/selectors';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import { Component, OnInit, OnChanges } from '@angular/core';
import { Product } from '../core/models/product';
import { Router, ActivatedRoute, Event as NavigationEvent } from '@angular/router';
import { UserService } from '../user/services/user.service'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  products$: Observable<any>;
  currentUser$: any;
  taxonId: number;
  taxon: any;

  constructor(private store: Store<AppState>, private actions: ProductActions, private route:ActivatedRoute, private router: Router, private userService: UserService) {
    router.events.subscribe((event:NavigationEvent)=>{
        this.getTaxon();
      }
   )
  }

  ngOnInit() {
    this.getTaxon();
    // this.store.dispatch(this.actions.getAllProducts());
    this.products$ = this.store.select(getProducts);
    if (localStorage.getItem('user')){
      this.userService.getUser().subscribe(res => {
        this.currentUser$ = res;
      });
    } else{
      this.currentUser$ = false;
    }
  }

  getTaxon(){
    this.taxonId = parseInt(this.route.snapshot.params['id']);

    const taxons = JSON.parse(localStorage.getItem('taxons'));
    for (let i = 0; i < taxons.length; i++){
      if (taxons[i]['id'] == this.taxonId)
        this.taxon = taxons[i];
    }
  }
}



